import { useEffect, useState } from "react"
import { getComics } from "../helper/getComics"


export const useFetchComics = (id, offset) => {
   
    const [state, setState] = useState({
        data: [],
        loading: true
    })


    useEffect(() => {
        getComics(id, offset)
            .then( comics => {
                setState({
                    data: comics,
                    loading: false
                })
            })
    }, [id, offset])

    return state
}
