import React, { useState } from 'react'
import { useFetchComics } from '../hook/useFetchComics'
import { ComicCard } from './ComicCard'

export const ComicGrid = ({ id, offset = 0 }) => {
    
    const [pagination, setPagination] = useState( offset )
    const { data:comics, loading } = useFetchComics( id, pagination )

    

    return (
        <div>
            
           {loading && 
                <div className="jumbotron bg-dark border border-dark rounded border-5  m-auto searching">
                        <div className="container p-5 text-center">
                            <h1 className="display-4 text-bold text-white">Searching comics</h1>
                            <div className="spinner-border  text-dark mt-5" role="status">
                                <span><i className="fas fa-spider fs-2 text-white"></i></span>
                            </div>
                        </div>
                </div>
            }
        
            <div className="d-flex flex-column flex-wrap hero-container">
               <div className="d-flex flex-wrap justify-content-around">
                    {
                        comics.map( comic => (
                            <ComicCard
                                key={comic.title}
                                {...comic }
                            />
                        ))
                    }
                </div>
                
                {!loading && (comics.length !== 0 ) 
                    && 
                    <div className="d-flex bg-dark justify-content-center p-3 footer">
                        <button 
                            className={"btn btn-primary  p-1 me-3 fs-4 btn-page"}
                            disabled={ pagination === 0 ?  true :  false}
                            onClick={() => setPagination(pagination - 6)}
                        >
                            Go Back
                            
                        </button>
                        <button 
                            className="btn btn-primary  me-3 fs-4 btn-page"
                            onClick={() => setPagination(pagination + 6)}
                        >
                            More Comics
                        </button>
                    </div>
                }
            </div>

        </div>
    )
}
