

export const getComics = async( id, offset = 0 ) => {
    const url = `https://gateway.marvel.com:443/v1/public/characters/${ id }/comics?ts=1&apikey=ce4fc258367d34ad6bd1c03ef33d0502&hash=b53720958766d1cd8f2156e60edec20a&offset=${offset}&limit=6`
    const res =  await fetch( url )
    const { data }  = await res.json()


    const comics = data.results.map( comic => {
        return{
            title: comic.title,
            desc: comic.description,
            page: comic.pageCount,
            img: comic.thumbnail.path
        }
    })


   return comics

}
