import React from 'react'

export const HeroCard = ({ name, id, img, desc }) => {
    
    const extension = '.jpg'
    
    return (
        <>
            <div className="card card-container">
                <img src={ img + extension} className="card-img-top heroImage" alt={name} />
                <div className="card-body">
                    <h3 className="card-title text-center">{ name }</h3>
                    <h6 className="card-text">{ desc }</h6>
                    <p className="card-text mt-4">Please use this code to search comics of this character: { id }</p>
                </div>
            </div>
        </>
    )
}
