import React from 'react'

export const ComicCard = ({title, desc, page, img}) => {
    let extension = '.jpg'
    return (
        <>
            <div className="card card-container">
                <img src={ img + extension} className="card-img-top heroImage" alt={title} />
                <div className="card-body">
                    <h3 className="card-title text-center">{ title }</h3>
                    <h6 className="card-text">{ desc }</h6>
                    <p className="card-text mt-4">Number of pages: { page }</p>
                </div>
            </div>
        </>
    )
}
