export const getHeroes = async ( name ) => {    
    // const auth = '?ts=1&apikey=ce4fc258367d34ad6bd1c03ef33d0502&hash=b53720958766d1cd8f2156e60edec20a'
    const url = `https://gateway.marvel.com:443/v1/public/characters?nameStartsWith=${encodeURI( name )}&ts=1&apikey=ce4fc258367d34ad6bd1c03ef33d0502&hash=b53720958766d1cd8f2156e60edec20a&limit=20` 
    const res = await fetch( url )
    const { data } = await res.json()

    const heros = data.results.map(hero => {
        return{
            id: hero.id,
            name: hero.name,
            desc: hero.description,
            img: hero.thumbnail.path,
            // comic: hero.comics.items
        }
    })
    

    return( heros )

}


