import React from 'react'
import { useFetchHeroes } from '../hook/useFetchHeroes'
import { HeroCard } from './HeroCard'

export const HeroGrid = ({ name }) => {
    
    const { data:heroes, loading } = useFetchHeroes( name )

    return (
        
        <div>
            {loading && 
                <div className="jumbotron bg-dark rounded m-auto searching">
                        <div className="container p-5 text-center">
                            <h1 className="display-4 text-bold text-white">Searching Hero</h1>
                            <div className="spinner-border  text-dark mt-5" role="status">
                            <span><i className="fas fa-spider fs-2 text-white"></i>.</span>
                            </div>
                        </div>
                </div>
            }

            {!loading && (heroes.length === 0 ) 
                &&  <div className="jumbotron d-flex bg-dark border border-dark rounded border-5 not-found">
                        <div className="container text-center mt-5 not-found__help">
                            <div className="text-bold text-white not-found__text">
                                <p className="mt-5 p-2"> 
                                    Hmm... My spider-sense do not detect any character with that identity please try again.
                                    <br />
                                    Here a little help from your friendly neibourgh Stan Lee told me that you should write the names exactly as they were created, like "spider-man"
                                </p>
                            </div>
                        </div>
                    </div>
            }
                <div className="hero-container">
                    {
                        heroes.map( hero => (
                            <HeroCard
                                key={hero.id}
                                {...hero }
                            />
                        ))
                    }
                </div>

            
        </div>
    )
}
