import {useEffect, useState } from 'react'
import { getHeroes } from '../helper/getHeroes'


export const useFetchHeroes = ( name ) => {

    const [state, setState] = useState({
        data:[],
        loading: true
    })

    
        useEffect(() => {
            getHeroes( name )
                .then( hero => {
                    setState({
                        data: hero,
                        loading: false
                    })
                })

        }, [name])

    return state;
}
